import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Box from '@material-ui/core/Box';

import productType from 'components/__types__/product';
import ProductFieldTable from 'components/product-field-table/ProductFieldTable';

const ProductDetails = ({ t, product }) => {
  return (
    <Box>
      <h3>
        {t('common.widgetName', {
          widgetNamePlaceholder: 'Product',
        })}
      </h3>
      <ProductFieldTable product={product} />
    </Box>
  );
};

ProductDetails.propTypes = {
  product: productType,
  t: PropTypes.func.isRequired,
};

ProductDetails.defaultProps = {
  product: {},
};

export default withTranslation()(ProductDetails);
